.header__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 1214px;
  padding-bottom: 19px;
}

.header__container {
  padding: 47px 20px 19px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px;
}

.header__form {
  position: relative;
  margin-right: 30px;
  flex-basis: 40%;
}

@media (max-width: $tablet-width) {
  .header__form {
    display: none;
  }
}

.header__form input {
  width: 206px;
  border: $white;
  padding: 6px 0 12px 29px;
}

.header__form input::placeholder {
  @include fonts(14px, 21px);
  font-family: "poppins-medium", "Arial", sans-serif;
}

.header__form input:hover {
  border-bottom: 1px solid $brown;
}

.header__form svg {
  position: absolute;
  top: 9px;
}

@media (max-width: $tablet-width) {
  .header__form svg{
    left: 15px;
  }
}

.header__logo {
  margin: 0 auto;
}

.header a {
  @include link();
  color: $almost-black;
  height: min-content;
  padding-bottom: 12px;
}

.header a:not(.header__logo):hover {
  border-bottom: 1px solid $brown;
}

.header a:active {
  color: $brown;
}

.header__users-links {
  flex-basis: 40%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.header__cart svg {
  display: none;
}

@media (max-width: $tablet-width) {
  .header__cart svg {
    display: block;
  }
}

@media (max-width: $tablet-width) {
  .header__cart span {
    display: none;
  }
}

@media (max-width: $tablet-width) {
  .header__login {
    display: none;
  }
}

.header__users-links a {
  width: max-content;
  margin-left: 35px;
}

.header__navigation {
  border-top: 1px solid $light;
  padding-top: 18px;
}

.header__navigation ul {
  @include list-reset();
  @include offset(0, 0);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0 auto;
}

.header__navigation li {
  @include offset(0, 0);
  width: max-content;
  padding-left: 35px;
}

.header__toggle {
  display: none;
}

@media (max-width: $tablet-width) {
  .header__toggle {
    display: block;
    border: none;
    background-color: $white;
    background-image: url("../img/open-button.svg");
    background-repeat: no-repeat;
    width: 55px;
    height: 55px;
    flex-basis: 40%;
  }
}

.header__additional-list {
  @include fonts(18px, 23px);
  @include list-reset();
  @include offset(0, 0);
  display: none;
  max-width: 80%;
}

.header__additional-list li {
  padding-bottom: 30px;
}

.header__additional-list a:not(.header__additional-special) {
  color: rgba($white, 0.75);
}

.header--active {
  background-color: $brown;
  padding: 55px 45px 80px;
}

.header--active .header__container {
  flex-wrap: wrap;
  padding-right: 0;
  padding-left: 0;
}

.header--active a {
  color: $white;
}

.header--active svg {
  stroke: $white;
}

.header--active .header__form {
  display: block;
  order: 3;
  flex-basis: 100%;
}

.header--active input {
  width: 100%;
  border-radius: 15px;
  background-color: rgba($white, 0.15);
}

.header--active .header__toggle {
  background-color: $brown;
  background-image: url("../img/active-button.svg");
}

.header--active .navigation {
  border-top: none;
}

.header--active .navigation__list {
  flex-direction: column;
  border-top: none;
  margin: 0;
}

.header--active .navigation__item {
  @include fonts(24px, 36px);
  padding-bottom: 40px;
  padding-left: 0;
}

.header--active .header__additional-list {
  display: flex;
  flex-direction: column;
}

.header--active .header__additional-special {
  padding-top: 25px;
}
